/* IMPORTS */

@import "https://fonts.googleapis.com/css?family=Montserrat&display=swap";

/* CUSTOM VARIABLES */

:root {
  --primary: hsl(190, 100%, 50%);
  --home-font: 'Montserrat', sans-serif;

  --dark-bg: hsl(0, 0%, 5%);
  --dark-text: hsl(0, 0%, 0%);
  --dark-hero: hsl(0, 0%, 15%);

  --light-bg: hsl(0, 0%, 100%);
  --light-text: hsl(0, 0%, 100%);
  --light-hero: hsl(0, 0%, 95%);

  --bg-color: var(--light-bg);
  --text-color: var(--dark-text);
  --hero-color: var(--light-hero);
}

/* CONTENT STYLING */

.custom {
  font-family: var(--home-font);
}

.hero {
  background-color: var(--light-hero);
}

.hero-sub {
  text-align: center;
}

.hero-logo {
  margin: 0 auto;
}

.tag {
  &.is-docker {
    background-color: #2496ED; 
    color: #fff;

    &.build {
      background-color: #FFFFFF;
    }
  }

  &.is-python {
    background-color: #3776AB; 
    color: #fff;

    &.build {
      background-color: #FFFFFF;
    }
  }
}

.building {
  display: flex;
  align-items: baseline;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
}

.tile:not(.is-child) {
  justify-content: center;
}

/* CUSTOM ELEMENTS */

.socials {
  display: flex;
  justify-content: center;
  align-items: center;
}

.social-button {
  margin: 0 auto;
  padding: 0.5rem;
  opacity: 80%;

  &:hover {
    transform: scale(1.1);
    opacity: 100%;
  }

  &#github {
    content: url("assets/img/github.png");
    height: 2rem;
  }

  &#twitter {
    content: url("assets/img/twitter.png");
    height: 2rem;
  }

  &#bulma {
    content: url("https://bulma.io/images/made-with-bulma.png");
    height: 2.5rem;
  }
}

#badge {
  margin: 0.25em;
  padding: 0.35rem;
  max-width: 90%;
  height: auto;

  &.docker {
    content: url("//img.shields.io/badge/docker-white?style=for-the-badge&logo=docker&logoColor=2496ED");
  }

  &.python {
    content: url("//img.shields.io/badge/python-white?style=for-the-badge&logo=python&logoColor=3776AB");
  }
}

#porpo-docker {
  content: url("assets/img/porpo-docker.png");
  width: 50%;

  &.roadmap-img {
    max-width: 250px;
  }
}

#porpo-desktop {
  content: url("assets/img/porpo-desktop.png");
  max-width: 550px;
  padding: .5rem;
}